
import React from 'react';
import styles from './style.css';

import bootstrap from 'bootstrap/dist/css/bootstrap.css';

import fontAwesome from 'font-awesome/css/font-awesome.css';

export default class HomePage extends React.Component {
    render() {
        return (
            <div className={bootstrap.container}>
                <div className={styles.sbContent}>
                    <h3 className={styles.sbTitle}>React Js Boilerplate</h3>
                    <p>play with react.</p>
                    <hr />
                    <div>
                        <ul className="list-inline intro-social-buttons">
                            <li>
                                <a href="https://github.com/ouranoshong/react-js-boilerplate" className={`${bootstrap.btn} ${bootstrap['btn-lg']} ${bootstrap['btn-default']}`}><i className={`${fontAwesome.fa} ${fontAwesome['fa-github']} ${fontAwesome['fa-fw']}`}></i> <span className="network-name">Github</span></a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        );
    }
}   


